import React ,{useState}from 'react';
import MenueTable from '../../Images/menueBack/brown-painted-wood-texture-wood-wall-background-texture.jpg';
import styled , {css} from 'styled-components';
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { BBQ, Biryani, Broasts, Burger, Drinks, Roll, Sandwiches } from '../../Data/ItemsOnMenue';
import { FaOpencart } from "react-icons/fa";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Basket from '../../Components/Basket';
const Container=styled.div`
`;

const Images=styled.img`

background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    align-items: center;
    justify-content: center;
    width: 98.7vw;
    height: 200vh;
    min-width: 400px;
`;

const MenueWrapper=styled.div`
background-color: #1c1c1cb3;
width: 70vw;
height: 180vh;
position: relative;
margin: 10px 40px;
border-radius: 10px;
h1{

    color: whitesmoke;
    margin: 0px auto;
    position: relative;
}
h5{
  margin-bottom: 0px;
}
h2{
  color: whitesmoke;
    margin: 5px auto;
}
`;
const Wrapper=styled.div`
margin-top: 5px;
`;

const cart=css`
width        : 40px;
height       : 40px;
color        : #fff;
cursor       : pointer;
background   : rgba(0,0,0,0.5);
border-radius: 10px;
padding      : 10px;
margin-right : 2px;
user-select  : none;
transition   : 0.3s;
float: right;
right: 0px;
//bottom: 2rem;
bottom: 4rem;
//right: 16px;
position: relative;

&:hover{
  background: #FEDE17;
  transform : scale(1.05);
}
`;

const AddtoCart = styled(FaOpencart)`
${cart}
`;
const ButtonWrapper=styled.div`
padding-left: 30px;

`;
const CardWrapper =styled.div`
  margin-top: 20px;
  margin-left: 20px;
`;


const MenueOfShop = () => {
          const [cartItems,setCartItems]=useState([]);
          //
          
          const onAdd = (products) => {
            const exist =cartItems.find((x) => x.id === products.id);
            if(exist){
              setCartItems(
                cartItems.map((x) =>
                  x.id === products.id ? {...exist, qty: exist.qty +1 } : x 
                )
              )
            } else {
              setCartItems([...cartItems, {...products, qty: 1}]);
            }
          };
          const onRemove = (products) => {
            const exist =cartItems.find((x) => x.id === products.id);
            if(exist.qty === 1){
              setCartItems(
                cartItems.filter((x) =>
                  x.id !== products.id
                  ))
            } else {
              setCartItems(
              cartItems.map((x) =>
              x.id === products.id ? {...exist, qty: exist.qty -1 } : x 
            )
            )
            }
          };


  var settings_3 = {
    dots            : false,
    infinite        : true,
    speed           : 500,
    slidesToShow    : 3,
    slidesToScroll  : 3,
    responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    
};

    return (
        <Container >
          
            <Images src={MenueTable} alt=''>
            </Images>
            <a href='https://www.freepik.com/photos/background'>Background photo created by ArthurHidden - www.freepik.com</a>
           <MenueWrapper>
               <div className="container">
  <div className="row">
    <div className="col-sm" >
      <h1>Menue</h1>
     
          <h2>BBQ</h2>
        <Slider {...settings_3} >
        {BBQ.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
        
        <h2>Burger</h2>
        <Slider {...settings_3} >
        {Burger.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
        <h2>Sandwich</h2>
        <Slider {...settings_3} >
        {Sandwiches.map((items,index) => (
                        <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                          
                            </ButtonWrapper>
                            </div>
                        </div>
                        </CardWrapper>
                    ))}
        </Slider>
        <h2>Roll</h2>
        <Slider {...settings_3} >
        {Roll.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
        <h2>Broast</h2>
        <Slider {...settings_3} >
        {Broasts.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
        <h2>Biryani</h2>
        <Slider {...settings_3} >
        {Biryani.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
        <h2>Drinks</h2>
        <Slider {...settings_3} >
        {Drinks.map((items,index) => (
                         <CardWrapper key = {index}>
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.itemPrice}</p>
                            <ButtonWrapper>
                            <AddtoCart onClick ={()=>onAdd(items)}/>
                            </ButtonWrapper>
                            </div>
                        </div>
                         </CardWrapper>
                    ))}
        </Slider>
    </div>
  </div>
</div> 
          </MenueWrapper>
          <Basket onAdd ={onAdd} onRemove={onRemove} cartItems={cartItems}/>
        </Container>
        
      
    )
}

export default MenueOfShop
