import React from 'react';
import styled from 'styled-components';
import Contact from '../ContactUs/Contact';
import DealsSection from '../MainHomePage/DealsSection';
import Footer from '../MainHomePage/Footer';
import Mainpage from '../MainHomePage/Mainpage';
import MenueSlider from '../MainHomePage/MenueSlider';
import {grabingDealsData} from '../../Data/GrabingDealsData';


const PageAndSliderConnector=styled.div`
text-align: center;
background-color: whitesmoke;
padding-top: 50px;

`;

const Home = () => {
    
    return (
        <>
        <Mainpage/>
  <PageAndSliderConnector>
    <h1 style={{marginBottom:'0rem'}}>Check items in Menue</h1>
  </PageAndSliderConnector>
   <MenueSlider id='slider'/> 
  <DealsSection grab ={grabingDealsData}/>
  <Contact id='contactUs'/>
  <Footer/>
  </>
    )
}

export default Home
