import React, { useState } from 'react';
import styled, {css} from 'styled-components';
import {RoundButton} from '../Components/RoundButton';
import {BsPlusCircleFill } from "react-icons/bs";
import {AiFillMinusCircle } from "react-icons/ai";
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import axios from 'axios';
import {toast} from 'react-toastify';
import '../../node_modules/react-toastify/dist/ReactToastify.css';
const Row=styled.div`
//display: block;
justify-content: center;
`;



const Cart=styled.div`
width: 24vw;
height: 100vh;
position: absolute;
background-color: whitesmoke;
right: 0px;
top: 0px;
border-radius: 10px;
h2{
    text-align: center;
}
h3{
    text-align: center;
}
h4{
    text-align: center;
   color: #FEDE17;
}
h6{
    color: white;
    margin-left: 1rem;
    font-size: larger;
}

`;

const UpperDivision=styled.div`
height: 67vh;
overflow-y: scroll;


`;
const RoundButtonWrapper=styled.div`
bottom: 10px;
left: 20%;
position: absolute;
`;

const InceDecreButtons=css`
width        : 40px;
height       : 40px;
color        : #fff;
cursor       : pointer;
background   : rgba(0,0,0,0.5);
border-radius: 10px;
padding      : 10px;
margin-right : 2px;
user-select  : none;
transition   : 0.3s;
float: right;

bottom: 15px;
position: relative;

&:hover{
  background: #FEDE17;
  transform : scale(1.05);
}
`;
const CardWrapper =styled.div`
  margin-top: 20px;
  margin-left: 20px;
`;
const Increament = styled(BsPlusCircleFill)`
${InceDecreButtons}
`;
const Decrement = styled(AiFillMinusCircle)`
${InceDecreButtons}
`;

const BottomDivison =styled.div`
bottom: 0px;
height: 30vh;
width: 100%;
background-color: rgba(0,0,0,0.5);
position: absolute;
`

toast.configure();
const Basket = (props) => {

   const notify = ()=> {
     
   }
  function randomString(length, chars) {
    var result = '';
    for (var i = length; i > 0; --i) result += chars[Math.floor(Math.random() * chars.length)];
    return result;
}
var rString = randomString(32, '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ');
    const {onAdd,onRemove,cartItems} = props;
    const Itemsprice =cartItems.reduce((a,c) => a+c.itemPrice*c.qty,0);
    const [name] =useState('ready to insert');
    const print = () => {
     
      {cartItems.map((items) => (

          axios.post("https://spinzerburger.com/api/insert",{
          CouponNumber: rString,
          itemName: items.itemName,
          Price: items.itemPrice,
          Qty: items.qty,
          totalPrice : items.itemPrice
       }).then(()=> {
       
         alert("Succesful insert");
       })
      ))}
      toast('Order Send !');
      
    }
    return (
        <Cart>
            <UpperDivision>
            <h2>Cart</h2>
            <div className="overflow-auto">
            <div>{cartItems.length === 0 && <h3>Cart is empty</h3>}</div>
            {cartItems.map((items) => (
                <Row key={items.id} >
                      <CardWrapper >
                        <div className = "card" style={{width: '18rem', height:'6rem'}}>
                          <div className = "card-body">
                            <h5 className  = "card-title" >{items.itemName}</h5>
                            <p className   = "card-text">{items.Itemsprice}</p>
                            <Increament onClick={() => onAdd(items)} ></Increament>
                        <Decrement onClick={() => onRemove(items)} ></Decrement>
                        {items.qty} x RS{items.itemPrice}
                            </div>
                        </div>
                         </CardWrapper>
                </Row>
            ))}
            </div>
            </UpperDivision>
            <hr></hr>
            <BottomDivison>
            <h4>Items Price</h4>
            <h6>RS {Itemsprice} </h6>
            <RoundButtonWrapper>
          <RoundButton  primary='true' style={{width:'20vw'}} onClick ={() => print()}>Order</RoundButton>
        </RoundButtonWrapper>
            </BottomDivison>
      </Cart>
    )
}

export default Basket
